import {Component} from '@angular/core';
import { ActivatedRoute,ParamMap } from '@angular/router'
import disableScroll from 'disable-scroll'; //nur da um scrollen zu deaktivieren
import { DataService } from "./data.service";


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent  {

  value!: string; //Gescannte Barcodenummer
  isError = false;
  scannerSichtbar = true; //Schalter um Video Livestream ein- oder aus zu blenden
  message: string;
  constructor(
    private data: DataService
    )
    {
      //disableScroll.on(); //Deaktiviert scrollen, falls nicht erwünscht
      this.data.currentMessage.subscribe(message => this.message=message); //Bis jetzt noch Platzhalter, hier muss REST Aufruf hin
    }

  onError(error: any) {
    console.error(error);
    this.isError = true;

  }

  onValueChange(){
    this.scannerSichtbar = false;
  }

  erneutScannen(){
      this.scannerSichtbar = true;
    }
  abschicken(){
    console.log('ID Säule:');
    console.log(this.message);
    console.log('Barcode:');
    console.log((<HTMLInputElement>document.getElementById("inputBarcodeScanner")).value); //Inhalt des Matinputfield wird hier abgerufen
  }
  }



